<template>
    <div class="main">
        <div class="dataDictionary">
            <div class="searchA">
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn(1,0)">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" v-if='tableData.length>0' border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%" row-key="id" lazy :load='load' :tree-props="{hasChildren: 'hasChildren'}">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip="true" v-if="item.prop == 'dictionary_type'">
                            <template slot-scope="scope">
                                <span v-if="scope.row.dictionary_type == 1">业务类</span>
                                <span v-else-if="scope.row.dictionary_type == 2">系统类</span>
                                <span v-else-if="scope.row.dictionary_type == 3">基础类</span>
                                <span v-else>其它类</span>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :width="item.widthS" v-else></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="220">
                        <template slot-scope="scope">
                            <el-button @click="addBtn(2,scope.row)" v-if="scope.row.level<3" type="text" size="small" style="color:#67c23a">新增下级字典</el-button>
                            <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" class="dialogStyle" v-dialogDrag :visible.sync="dialogVisible" width="600px" v-if="dialogVisible" :close-on-click-modal="false">
                <div>
                    <el-form :model="formData" ref="formData" :rules="rule" class="demo-ruleForm">
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="dictionary_name" label="字典类型">
                                        <el-input v-model="formData.dictionary_name" placeholder="请输入字典类型" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item prop="dictionary_code" label="字典编码">
                                        <el-input v-model="formData.dictionary_code" placeholder="请输入字典编码" size="small" :disabled="codeDisabled"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="order_num" label="列表排序">
                                        <el-input v-model="formData.order_num" placeholder="请输入列表排序" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="字典数值">
                                        <el-input v-model="formData.dictionary_value" placeholder="请输入字典数值" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput dialogInput1">
                            <div class="padd">
                                <div>
                                    <el-form-item label="字典分类">
                                        <el-radio-group v-model="dictionary_type">
                                            <el-radio :label="1">业务类</el-radio>
                                            <el-radio :label="2">系统类</el-radio>
                                            <el-radio :label="3">基础类</el-radio>
                                            <el-radio :label="4">其它类</el-radio>
                                        </el-radio-group>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div class="paddTit">字典说明</div>
                                <div>
                                    <el-form-item>
                                        <el-input v-model="formData.remarks" type="textarea" placeholder="请输入字典说明(100个字符以内)" maxlength='100' :autosize="{ minRows: 2, maxRows: 4}"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div style="text-align: center">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>
                    </el-form>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    dataDictionaryListApi,
    dataDictionaryZdDelApi,
    dataDictionaryZdListApi,
    dataDictionaryZdAddApi,
    dataDictionaryZdEditApi,
    dataDictionaryZdInfoApi,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            dictionary_type: 1,
            formData: {
                dictionary_name: '',
                dictionary_code: "",
                remarks: "",
                order_num: "",
                dictionary_value: ""
            },
            method_auth: {},
            editId: '',
            title: '',
            dialogVisible: false,
            rule: {
                dictionary_name: [
                    { required: true, message: '请输入项目名称', trigger: 'blur' },
                ],
                dictionary_code: [
                    { required: true, message: '请输入参数值', trigger: 'blur' },
                ],
            },
            tableHead: [
                { title: 'ID', prop: 'id', widthS: "130" },
                { title: '字典类型', prop: 'dictionary_name' },
                { title: '字典编码', prop: 'dictionary_code' },
                { title: '字典分类', prop: 'dictionary_type' },
                { title: '字典说明', prop: 'remarks' },

            ],
            tableData: [],
            hashData: '',
            parent_id: "",
            codeDisabled: false,
            level: 0,//数据层级
        };
    },
    mounted() {
        this.getListData();
    },
    methods: {
        async load(tree, treeNode, resolve) {
            //获取字典列表
            const { data } = await this.axios.post(dataDictionaryZdListApi, {
                curr: 1,
                pagesize: 50,
                id: tree.id
            });
            if (data.code == 1) {
                resolve(data.data.data)
            } else {
                this.$message({ showClose: true, message: data.msg, type: 'error', });
            }
        },

        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getListData();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getListData();
        },
        //获取Hash
        async getHashData() {
            const res = await this.axios.post(hashApi, { page_hash: 'dataDictionary' });
            if (res.data.code == 1) {
                this.hashData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        // 新增编辑提交
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    let datas = {
                        dictionary_type: this.dictionary_type,
                        dictionary_name: this.formData.dictionary_name,
                        dictionary_code: this.formData.dictionary_code,
                        order_num: this.formData.order_num,
                        dictionary_value: this.formData.dictionary_value,
                        remarks: this.formData.remarks,
                        hash: this.hashData,
                        level: this.level,
                    };
                    if (this.title.indexOf("新增") != -1) {
                        var urls = dataDictionaryZdAddApi;
                        datas.parent_id = this.parent_id;
                    } else if (this.title == '编辑') {
                        var urls = dataDictionaryZdEditApi;
                        datas.id = this.editId;
                    }
                    const res = await this.axios.post(urls, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.tableData = [];
                        this.$nextTick(() => {
                            this.getListData();
                        });
                    } else {
                        this.getHashData();
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error',
                        });
                    }
                } else {
                    return false;
                }
            });
        },
        //重置对话框表单
        resetForm() {
            this.dictionary_type = 1;
            this.formData = {
                dictionary_name: '',
                dictionary_code: '',
                order_num: "",
                dictionary_value: "",
                remarks: '',
            };
        },
        // 点击新增
        addBtn(e, s) {
            this.dialogVisible = true;
            this.codeDisabled = false;
            if (e == 1) {
                this.parent_id = s;
                this.title = '新增父级字典';
                this.level = 1;
            } else if (e == 2) {
                this.parent_id = s.id;
                this.title = '新增' + s.dictionary_name + '下级字典';
                this.level = s.level + 1
            }
            this.getHashData();
            this.resetForm(); //重置表单
        },
        // 点击编辑
        editBtn(row) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.codeDisabled = true;
            this.dictionary_type = row.dictionary_type;
            this.editId = row.id;
            this.getInfo(row.id);
            this.getHashData();
            this.$nextTick(() => {
                this.$refs['formData'].clearValidate();
            });
        },
        //字典详情
        async getInfo(id) {
            const res = await this.axios.post(dataDictionaryZdInfoApi, {
                id: id,
            });
            if (res.data.code == 1) {
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                })
            }
        },
        //删除
        async delBtn(id) {
            this.$confirm('是否删除该条数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            })
                .then(async () => {
                    const datas = {
                        id: id,
                    };
                    const res = await this.axios.post(dataDictionaryZdDelApi, datas);
                    if (res.data.code == 1) {
                        this.tableData = [];
                        this.$nextTick(() => {
                            this.getListData();
                        });
                        this.$message({ showClose: true, message: '删除成功', type: 'success', });
                    } else {
                        this.$message({ showClose: true, message: res.data.msg, type: 'error', });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除',
                    });
                });
        },
        //获取列表
        async getListData() {
            this.isLoading = true;
            let datas = {
                curr: this.pageNum,
                pagesize: this.pageSize,
            };
            const { data } = await this.axios.post(dataDictionaryListApi, datas);
            if (data.code == 1) {
                this.tableData = data.data.data;
                this.total = data.data.total;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({ showClose: true, message: data.msg, type: 'error', });
            }
        }
    }
};
</script>
<style >
.dataDictionary .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.dataDictionary {
    /deep/ .dialogStyle {
        .el-form-item {
            margin-bottom: 20px !important;
        }

        .el-form-item__label {
            line-height: 20px;
        }

        .dialogInput1 {
            .el-form-item {
                margin-bottom: 0px !important;
            }
            .el-form-item__label {
                line-height: 40px;
            }
        }
        .el-form--label-top .el-form-item__label {
            padding: 0;
        }
        .el-dialog__body {
            padding: 30px 15px 30px 25px;
        }
        .el-input-number .el-input__inner {
            text-align: left;
        }
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 10px;
        .padd {
            flex: 1;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
        .el-select {
            width: 100%;
        }
    }
    .btn {
        width: 250px;
    }
}
</style>



